export default {
	data() {
		return {
			imglogo: "",
			merchant: "",
			userID: "",
			spread: "",
			tel: ""
		}
	},
	onLoad() {
		this.__shop()
	},
	methods: {
		async __shop() {
			this.$H.post("/merchant/", {
				user_id: "183823",
				token: "dXQyMDIwMDMxNjE4MDkxOTgxNTU5NjA2",
				opt: "merchant_info"
			}).then((data) => {
				console.log(data);
				this.imglogo = data.logo_url
				this.userID = data.user_id
				this.spread = 0
				this.merchant = data.merchant_name
				this.tel = data.merchant_tel
			}).catch((data) => {
				console.log('--catch 错误！！');
			})
			
		}
		
	}
}
